import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { RecordId } from '../types';
import { ActivatedRoute, Params } from '@angular/router';
import { ApiService } from '../shared/services/api.service';
import { ApiRecord } from '../shared/entities/api-record';

@Component({
  selector: 'app-play',
  templateUrl: './play.component.html',
  styleUrls: ['./play.component.styl'],
})
export class PlayComponent implements OnInit, OnDestroy {
  subscription: Subscription;
  record: ApiRecord;
  error = false;

  constructor(
    private route: ActivatedRoute,
    private apiService: ApiService,
  ) {
  }

  get recordCanBeDeleted() {
    return this.record && this.record.owner;
  }

  ngOnInit() {
    this.subscription = this.route.params
      .subscribe(params => this.onRouteParamsChange(params));
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  private onRouteParamsChange(routeParams: Params) {
    this.onRecordIdChange(routeParams['id']);
  }

  private onRecordIdChange(recordId: RecordId) {
    if (recordId) {
      this.apiService.getRecord(recordId)
        .subscribe(
          record => this.record = record,
          error => this.error = true,
        );
    }
  }
}



