import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { ApiRecord } from '../../../shared/entities/api-record';

@Component({
  selector: 'app-player',
  templateUrl: './player.component.html',
  styleUrls: ['./player.component.styl']
})
export class PlayerComponent implements OnInit {
  duration = 0;
  currentTime = 0;
  requestAnimationFrameId: number;

  @Input() record: ApiRecord;

  @ViewChild('audioElement') audioElement: ElementRef;

  constructor() {
  }

  ngOnInit() {
  }

  get audioNativeElement(): HTMLAudioElement {
    return this.audioElement.nativeElement as HTMLAudioElement;
  }

  get percentPosition(): number {
    if (this.duration) {
      const percent = 100 * this.currentTime / this.duration;
      return percent > 100 ? 100 : percent;
    } else {
      return 0;
    }
  }

  play() {
    this.audioNativeElement.play();
    this.requestAnimationFrame();
  }

  requestAnimationFrame() {
    this.requestAnimationFrameId = requestAnimationFrame(() => this.animate());
  }

  animate() {
    this.currentTime = this.audioNativeElement.currentTime;

    this.requestAnimationFrame();
  }

  pause() {
    cancelAnimationFrame(this.requestAnimationFrameId);

    this.audioNativeElement.pause();
  }

  onTimeUpdate() {
    this.currentTime = this.audioNativeElement.currentTime;
  }

  onLoadedMetaData() {
    this.duration = this.audioNativeElement.duration;
  }

  onScaleClick(event: MouseEvent) {
    let currentTime = this.duration * this.getScaleClickCoefficient(event);

    if (currentTime > this.duration) {
      currentTime = this.duration;
    }

    this.audioNativeElement.currentTime = currentTime;
  }

  private getScaleClickCoefficient(event: MouseEvent): number {
    const clientX = event.clientX;
    const scaleNativeElement: HTMLDivElement = event.currentTarget as HTMLDivElement;
    const scaleLeft = scaleNativeElement.getBoundingClientRect().left;
    const scaleOffsetWidth = scaleNativeElement.offsetWidth;

    return (clientX - scaleLeft) / scaleOffsetWidth;
  }
}



