import { Component, Input, OnInit } from '@angular/core';
import { ApiRecord } from '../../../shared/entities/api-record';
import { ApiService } from '../../../shared/services/api.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-play-delete-record',
  templateUrl: './play-delete-record.component.html',
  styleUrls: ['./play-delete-record.component.styl'],
})
export class PlayDeleteRecordComponent implements OnInit {

  @Input() record: ApiRecord;

  constructor(
    private apiService: ApiService,
    private router: Router,
  ) {
  }

  ngOnInit() {
  }

  onButtonClick(event: MouseEvent) {
    const buttonElement: HTMLButtonElement = event.target as HTMLButtonElement;

    if (confirm(buttonElement.title)) {
      this.apiService.deleteRecord(this.record.id)
        .subscribe(() => this.router.navigate(['/']));
    }
  }

}



